.news b,
.news strong {
  font-weight: bolder; }

.news img {
  border-style: none; }

.news button {
  font-size: 100%;
  overflow: visible;
  text-transform: none; }

.news button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0; }

.news button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText; }

.news [hidden] {
  display: none; }

@font-face {
  font-family: "Roboto";
  src: url("../../fonts/Roboto-Regular.woff2") format("woff2"), url("../../fonts/Roboto-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap; }

@font-face {
  font-family: "Roboto";
  src: url("../../fonts/Roboto-Bold.woff2") format("woff2"), url("../../fonts/Roboto-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap; }

.playlist {
  width: 1440px;
  height: 100px;
  margin: 0 auto;
  outline: 4px solid green;
  padding-left: 20px;
  padding-right: 20px; }

.playlist__songs {
  display: block;
  width: 675px;
  height: 100px;
  outline: 4px solid tomato; }

.playlist__mood {
  padding-left: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  list-style: none; }

.playlist__tone {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  cursor: pointer;
  width: 41px;
  height: 28px;
  -webkit-filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
          filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25)); }

.badge {
  font-family: "Roboto";
  color: white;
  font-size: 18px;
  line-height: 21px;
  font-weight: 700;
  border-radius: 5px; }

